import { useEffect, useState } from 'react';
import { FloatButton } from 'antd';
import { ClockCircleOutlined } from '@ant-design/icons';
import * as THREE from 'three';
import TWEEN from '@tweenjs/tween.js';
import { POINTS_MAPS } from '../../constant';
import '../index.css';

const SwitchSceneBackground = ({ objects, current, render }) => {
  const [isAuto, setIsAuto] = useState(true);

  const setPointsMap = num => {
    if (!objects) return;
    const { points } = objects;
    const newTexture = new THREE.TextureLoader().load(POINTS_MAPS[num || 0]);
    //  Tween 
    new TWEEN.Tween({ t: 0 })
      .to({ t: 1 }, 400) //  1000 
      .easing(TWEEN.Easing.Linear.None) //  Quadratic.Out 
      .onUpdate(({ t }) => {
        // 
        if (t > 0.5) {
          points.material.map = newTexture;
        }
      })
      .onComplete(() => {
        render();
      })
      .start();
  };

  useEffect(() => {
    if (isAuto && objects) {
      setPointsMap(THREE.MathUtils.randInt(0, 7));
    }
  }, [current, objects]);

  const onPointsMapChange = num => {
    setPointsMap(num);
  };

  return (
    <FloatButton.Group shape="square">
      <FloatButton
        type={isAuto ? 'primary' : 'default'}
        icon={<ClockCircleOutlined className="theme-icon" />}
        tooltip={isAuto ? '' : ''}
        onClick={() => setIsAuto(!isAuto)}
      />
      <FloatButton description="" onClick={() => onPointsMapChange(0)} />
      <FloatButton description="" onClick={() => onPointsMapChange(1)} />
      <FloatButton description="" onClick={() => onPointsMapChange(2)} />
      <FloatButton description="" onClick={() => onPointsMapChange(3)} />
      <FloatButton description="" onClick={() => onPointsMapChange(4)} />
      <FloatButton description="" onClick={() => onPointsMapChange(5)} />
      <FloatButton description="" onClick={() => onPointsMapChange(6)} />
      <FloatButton description="" onClick={() => onPointsMapChange(7)} />
    </FloatButton.Group>
  );
};

export default SwitchSceneBackground;
